.page-company {
  #company {
    p {
      margin: 0;
      padding: 0;
    }
    h4 {
      border-left-width: 7px;
      border-left-style: solid;
      border-left-color: #ff944d;
      padding: 8px;
      margin-top: 5px;
      margin-right: 5px;
      margin-bottom: 5px;
      margin-left: 3px;
      font-size: 16px;
      font-weight: bold;
      text-align: left;
    }
    .c-box {
      margin: 0 auto;
      text-align: center;
      img.mb10, iframe {
        margin-bottom: 10px;
      }
    }
    .company__tbl {
      width: 100%;
      margin: 0 auto;
    }
    &.index_2 {
      div.staff {
        border-bottom: 1px dashed #333;
        margin-bottom: 20px;
        padding-bottom: 20px;
        .pic {
          text-align: center;
          img {
            width: 105px;
            height: auto;
          }
        }
        .name {
          font-weight: 700;
          @include font-line(16, 20);
          color: $color-primary;
          margin-bottom: 5px;
        }
        dl {
          text-align: left;
          margin-bottom: 0;
          dt {
            font-weight: 700;
            @include font-line(14, 18);
            margin-top: 1em;
          }
          dd {
            text-indent: 1em;
          }
        }
      }
    }
  }

  .l-content {
    p {
      @include font-line(14);
    }
    .buttonBlock {
      text-align: center;
      &__btn {
        width: 100%;
      }
    }
  }

  .map {
    text-align: center;
    margin-bottom: 30px;
    &__ttl{
      @include font-line(20, 20);
      margin-bottom: 10px;
    }
    .button {
      height: 30px;
      width: 60%;
      margin: 0 auto;
    }
  }

  section {
    &.index_2 .wrap {
      width: 96%;
      margin: 0 auto;
      text-align: left;
      div.pic {
        text-align: center;
      }
      p.name {
        font-weight: bold;
        color: #036f35;
      }
      dl {
        text-align: left;
        margin-bottom: 1em;
        dt {
          font-weight: bold;
        }
        dd {
          text-indent: 1em;
        }
      }
      div.line {
        margin-bottom: 1em;
      }
    }
    &.index_1 {
      .wrap {
        width: 96%;
        margin: 0 auto;
        text-align: left;
        ul {
          margin: 0 auto 1em;
        }
      }
      .shomei {
        text-align: right;
        margin: 0 auto 1em;
        span {
          font-size: 15px;
        }
      }
    }
  }
}
